/* settings */
$burder-color: $dark-blue

.hamburger
  padding: 0
  display: flex
  cursor: pointer
  transition-property: opacity, filter
  transition-duration: 0.15s
  transition-timing-function: linear
  font: inherit
  color: inherit
  text-transform: none
  background-color: transparent
  border: 0
  overflow: visible
  position: relative
  order: 5
  z-index: 20
.hamburger-box
  width: 22px
  height: 22px
  display: inline-block
  position: relative
.hamburger-inner
  display: block
  top: 50%
  margin-top: -2px
  width: 22px
  height: 3px
  background-color: $burder-color
  border-radius: 4px
  position: absolute
  transition-property: transform
  transition-duration: 0.15s
  transition-timing-function: ease
  &::before, &::after
    width: 22px
    height: 3px
    background-color: $burder-color
    border-radius: 4px
    position: absolute
    transition-property: transform
    transition-duration: 0.15s
    transition-timing-function: ease
  &::before, &::after
    content: ""
    display: block
  &::before
    top: -8px
  &::after
    bottom: -8px

/* slide animate */
.hamburger--slider
  .hamburger-inner
    top: 3px
    &::before
      top: 8px
      transition-property: transform, opacity
      transition-timing-function: ease
      transition-duration: 0.15s
    &::after
      top: 16px
  &.is-active .hamburger-inner
    transform: translate3d(0, 8px, 0) rotate(45deg)
    &::before
      transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0)
      opacity: 0
    &::after
      transform: translate3d(0, -16px, 0) rotate(-90deg)