@include font-face("Fontello", "fontello", 400, normal)

[class^="icon-"]:before,
[class*=" icon-"]:before
  font-family: Fontello
  font-style: normal
  font-weight: normal
  speak: never
  display: inline-block
  margin: 0 auto
  text-decoration: inherit
  text-align: center
  font-variant: normal
  text-transform: none

/* icons */
.icon-arrow-down_icon:before
  content: '\e800'
.icon-arrow-right_icon:before
  content: '\e801'
.icon-bread-arrow_icon:before
  content: '\e802'
.icon-cart_icon:before
  content: '\e803'
.icon-check_icon:before
  content: '\e804'
.icon-close_icon:before
  content: '\e805'
.icon-home_icon:before
  content: '\e806'
.icon-map-pin_icon:before
  content: '\e807'
.icon-menu_icon:before
  content: '\e808'
.icon-menu-change_icon:before
  content: '\e809'
.icon-phone_icon:before
  content: '\e80a'
.icon-search_icon:before
  content: '\e80b'