[class^=icon-]:before,
[class*=" icon-"]:before {
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  margin: 0 auto;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
}

/* icons */
.icon-arrow-down_icon:before {
  content: "\e800";
}

.icon-arrow-right_icon:before {
  content: "\e801";
}

.icon-bread-arrow_icon:before {
  content: "\e802";
}

.icon-cart_icon:before {
  content: "\e803";
}

.icon-check_icon:before {
  content: "\e804";
}

.icon-close_icon:before {
  content: "\e805";
}

.icon-home_icon:before {
  content: "\e806";
}

.icon-map-pin_icon:before {
  content: "\e807";
}

.icon-menu_icon:before {
  content: "\e808";
}

.icon-menu-change_icon:before {
  content: "\e809";
}

.icon-phone_icon:before {
  content: "\e80a";
}

.icon-search_icon:before {
  content: "\e80b";
}

/* all */
* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p, ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

button, label {
  cursor: pointer;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

img {
  max-width: 100%;
}

/* main */
body {
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
  position: relative;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #1B2735;
  background-color: #fff;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #2B99FF;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #57a1ff;
}
body::-webkit-scrollbar-track {
  background: #11489B;
  border-radius: 0;
}
body {
  scrollbar-face-color: #2B99FF;
  scrollbar-track-color: #11489B;
}
body.overflow {
  overflow: hidden;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 1025px) {
  .container {
    max-width: 1200px !important;
  }
}

@media (min-width: 1200px) {
  .header {
    max-width: 100vw;
  }
}
.header.scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  background-color: #fff;
  z-index: 20;
  transform: translateY(-120%);
  transition: none;
  border-bottom: 1px solid #C8E0FF;
}
@media (min-width: 1200px) {
  .header.scroll {
    padding-right: 8px;
  }
}
.header.scroll.show {
  transition: transform 0.3s;
  transform: translateY(0);
}
.header.scroll .header__content__top {
  display: none !important;
}
.header.scroll .header__content__bottom {
  padding: 5px 0;
}
.header.scroll .header__content__bottom:after {
  display: none;
}
.header.scroll .header__content__bottom .hbottom-backcall {
  display: block !important;
  white-space: nowrap;
  margin: 0;
  order: 5;
}
.header.scroll .header__content__bottom .hbottom-backcall .backcall-link {
  padding: 9px 12px;
}
.header.scroll .header__content__bottom .logo {
  flex: 0 0 75px;
  max-width: 75px;
  margin: 0 20px -20px 0;
}
.header.scroll .header__content__bottom .logo .logo-link {
  position: relative;
  left: 0;
  bottom: 0;
}
@media (min-width: 1200px) {
  .header.scroll .header__content__bottom .hbottom-search {
    display: block !important;
    order: 4;
    margin: 0;
    z-index: 1;
    margin-right: 15px;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 100vw;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group button {
    background-color: transparent;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: none;
    padding: 10px 9px;
    position: absolute;
    left: 5px;
    top: 0;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group button i.icon {
    font-size: 18px;
    line-height: 18px;
    margin-top: 1px;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group button i.icon:before {
    font-weight: 500;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input {
    position: relative;
    right: 0;
    width: 100%;
    min-width: 220px;
    background-color: #E2EFFF;
    border: 1px solid transparent;
    border-radius: 4px;
    padding: 9px 15px 8px 45px;
    font-size: 15px;
    line-height: 21px;
    letter-spacing: 0.02em;
    transition: background-color 0.3s, border-color 0.3s, max-width 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input.placeholder {
    color: #11489B;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input::-webkit-input-placeholder {
    color: #11489B;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input::-moz-placeholder {
    color: #11489B;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:-moz-placeholder {
    color: #11489B;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:-ms-input-placeholder {
    color: #11489B;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus {
    background-color: #fff;
    border-color: #1D6AFF;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus.placeholder {
    opacity: 0.3;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus::-webkit-input-placeholder {
    opacity: 0.3;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus::-moz-placeholder {
    opacity: 0.3;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus:-moz-placeholder {
    opacity: 0.3;
  }
  .header.scroll .header__content__bottom .hbottom-search .search-form .input-group input:focus:-ms-input-placeholder {
    opacity: 0.3;
  }
}
.header.scroll .header__content__bottom .hbottom-menu .main-menu__list {
  margin: 0 0 0 15px;
}
@media (min-width: 1200px) {
  .header__content__top {
    display: flex !important;
    position: relative;
    color: #fff;
    padding: 28px 0 28px 170px;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }
  .header__content__top:before {
    content: "";
    display: block;
    width: 100vw;
    height: 100%;
    background-color: #009EF6;
    position: absolute;
    left: 50%;
    top: 0;
    z-index: -1;
    transform: translateX(-50%);
  }
  .header__content__top .htop-time {
    margin-left: 28px;
    display: inline-block;
  }
  .header__content__top .htop-email {
    margin-left: 29px;
  }
  .header__content__top .htop-email .mail-link {
    color: #fff;
  }
  .header__content__top .htop-phone {
    margin-left: 28px;
  }
  .header__content__top .htop-phone .phone-link {
    color: #fff;
  }
  .header__content__top .htop-backcall {
    margin: -12px 0 -12px auto;
  }
  .header__content__top .htop-backcall .backcall-link {
    padding: 12px 16px;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }
}
.header__content__bottom {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 5px 15px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 20;
  border-bottom: 1px solid #C8E0FF;
}
@media (min-width: 577px) {
  .header__content__bottom {
    padding: 5px 24px 3px;
  }
}
@media (min-width: 1200px) {
  .header__content__bottom {
    position: relative;
    padding: 15px 0 17px;
    border-bottom: none;
  }
  .header__content__bottom:after {
    content: "";
    display: block;
    width: 100vw;
    height: 100%;
    position: absolute;
    left: 50%;
    right: 50%;
    transform: translateX(-50%);
    bottom: 0;
    box-shadow: 0px 4px 20px rgba(47, 73, 113, 0.06);
    z-index: -1;
  }
}
@media (min-width: 577px) {
  .header__content__bottom .hbottom-phone {
    display: block !important;
    order: 2;
    margin-left: 20px;
  }
  .header__content__bottom .hbottom-phone .phone-link {
    display: inline-block;
    font-weight: 800;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: -0.04em;
    color: #11489B;
    transition: color 0.3s;
  }
  .header__content__bottom .hbottom-phone .phone-link:hover {
    color: #2B99FF;
  }
}
@media (min-width: 1200px) {
  .header__content__bottom .hbottom-phone {
    display: none !important;
  }
}
.header__content__bottom .logo {
  flex: 0 0 75px;
  display: block;
  margin: 0 auto -15px;
}
.header__content__bottom .logo .logo-link {
  display: block;
}
@media (min-width: 577px) {
  .header__content__bottom .logo {
    order: 1;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .header__content__bottom .logo {
    flex: 0 0 140px;
    margin: 0;
    position: relative;
    height: 100%;
  }
  .header__content__bottom .logo .logo-link {
    display: block;
