@media (min-width: 1200px) {
  .header__content__bottom .hbottom-backcall {
    display: none;
  }
}
.header__content__bottom .hbottom-menu {
  /* settings */
  /* slide animate */
}
.header__content__bottom .hbottom-menu .hamburger {
  padding: 0;
  display: flex;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  order: 5;
  z-index: 20;
}
.header__content__bottom .hbottom-menu .hamburger-box {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: relative;
}
.header__content__bottom .hbottom-menu .hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
  width: 22px;
  height: 3px;
  background-color: #11489B;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content__bottom .hbottom-menu .hamburger-inner::before, .header__content__bottom .hbottom-menu .hamburger-inner::after {
  width: 22px;
  height: 3px;
  background-color: #11489B;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content__bottom .hbottom-menu .hamburger-inner::before, .header__content__bottom .hbottom-menu .hamburger-inner::after {
  content: "";
  display: block;
}
.header__content__bottom .hbottom-menu .hamburger-inner::before {
  top: -8px;
}
.header__content__bottom .hbottom-menu .hamburger-inner::after {
  bottom: -8px;
}
.header__content__bottom .hbottom-menu .hamburger--slider .hamburger-inner {
  top: 3px;
}
.header__content__bottom .hbottom-menu .hamburger--slider .hamburger-inner::before {
  top: 8px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.header__content__bottom .hbottom-menu .hamburger--slider .hamburger-inner::after {
