@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_fonts/fonts_list"                                                                                             // fonts
@import "_fonts/fontello"                                                                                            // icons font
@import "_main/reset"                                                                                                  // reset styles
@import "_main/header"                                                                                                 // header

/* main page */
main
  margin-top: 58px
  @media (min-width: 577px)
    margin-top: 76px
  @media (min-width: 1200px)
    margin-top: 0
.main-page
  color: #fff
  padding-top: 33px
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  margin-bottom: 10px
  @media (min-width: 577px)
    padding-top: 40px
    margin-bottom: 13px
  @media (min-width: 1200px)
    padding-top: 58px
  .container
    @media (min-width: 577px)
      max-width: 600px
      margin: 0 auto
    @media (min-width: 961px)
      max-width: 100%
  &__title
    text-align: center
    font-family: Oswald, sans-serif
    font-weight: 700
    letter-spacing: -.02em
    @include adaptive-value('font-size', 46, 28, 1)
    @include adaptive-value('line-height', 68, 27, 1)
    max-width: 400px
    margin: 0 auto
    @media (min-width: 577px)
      font-size: 46px
      line-height: 40px
      max-width: 100%
      font-weight: 600
  &__subtitle
    display: block
    margin-top: 15px
    @include adaptive-value('font-size', 24, 15, 1)
    @include adaptive-value('line-height', 28, 21, 1)
    font-weight: 400
    letter-spacing: .02em
    text-align: center
    @media (min-width: 577px)
      font-size: 24px
      line-height: 28px
      font-weight: 500
      margin-top: 18px
    @media (min-width: 961px)
      max-width: 700px
      margin: 18px auto 0
    @media (min-width: 1200px)
      margin: 22px auto 0
      letter-spacing: -0.02em
  &__content
    margin-top: 40px
    display: flex
    align-items: stretch
    justify-content: space-between
    flex: 1 1 100%
    max-width: 100%
    width: 100%
    overflow-x: hidden
    @media (min-width: 577px)
      flex-wrap: wrap
      justify-content: flex-start
      margin-top: 50px
    @media (min-width: 1200px)
      margin-top: 16.75rem
    &__item
      position: relative
      flex: calc(33.3% - 4px)
      max-width: calc(33.3% - 4px)
      @media (min-width: 577px)
        flex: 1 1 100%
        max-width: 100%
        width: 100%
        margin-bottom: 30px
        background: rgba(17, 141, 255, 0.85)
        backdrop-filter: blur(40px)
        border-radius: 6px
      @media (min-width: 961px)
        flex: calc(33.3% - 7px)
        max-width: calc(33.3% - 7px)
        margin: 0 11px 0 0
        &:nth-of-type(3)
          margin-right: 0
      @media (min-width: 1200px)
        flex: calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        margin-right: 30px
      .title-block
        background-color: rgba(0, 117, 225, 0.7)
        backdrop-filter: blur(40px)
        transition: background-color .3s
        border-radius: 8px
        text-align: center
        padding: 15px 5px 10px
        min-height: 135px
        @media (min-width: 415px)
          min-height: 150px
          padding: 15px 10px
        @media (min-width: 577px)
          display: flex
          flex: 1 1 100%
          align-items: flex-start
          justify-content: flex-start
          border-radius: 0
          padding: 24px 30px
          min-height: auto
          background: none !important
          backdrop-filter: none !important
          .item-title-mobile
            display: none
          .item-title
            display: block !important
            flex: 1 1 auto
            max-width: 340px
            width: 100%
            text-align: left
            font-family: Oswald, sans-serif
            font-weight: 700
            font-size: 25px
            line-height: 25px
            padding-left: 32px
            padding-top: 10px
        @media (min-width: 961px)
          flex-direction: column
          padding: 25px 25px 30px
          .item-title
            padding: 10px 0 0
            text-align: center
            min-height: 110px
        @media (min-width: 1200px)
          padding: 42px 30px 7px
        .image
          .item-image
            display: block
            max-width: 45px
            margin: 0 auto
          @media (min-width: 577px)
            flex: 0 0 95px
            width: 100%
            max-width: 95px
            .item-image
              display: block
              width: 100%
              max-width: 100%
              margin: 0 auto
          @media (min-width: 961px)
            margin: 0 auto
          @media (min-width: 1200px)
            margin-bottom: 22px
        .item-title-mobile
          font-family: Oswald, sans-serif
          font-weight: 500
          @include adaptive-value('font-size', 45, 14, 1)
          @include adaptive-value('line-height', 40, 15, 1)
          letter-spacing: -.02em
          white-space: break-spaces
          margin-top: 8px
          text-align: center
          @media (min-width: 360px)
            span
              display: none
          @media (min-width: 415px)
            font-size: 20px
            line-height: 22px
      .descr-block
        position: relative
        width: calc(100vw - 30px)
        background-color: #1F8BED
        padding: 18px 18px 24px
        border-radius: 6px
        margin: 8px 0
        opacity: 0
        transition: opacity .3s
        @media (min-width: 577px)
          opacity: 1
          left: 0 !important
          max-width: 100%
          padding: 7px 30px 30px 30px
          margin: 0
          border-radius: 0
          background: none !important
          backdrop-filter: none !important
        @media (min-width: 961px)
          padding: 0 25px 25px
        @media (min-width: 1200px)
          padding: 0 32px 42px
        .item-title-mobile-full
          font-family: Oswald, sans-serif
          font-weight: 700
          @include adaptive-value('font-size', 50, 20, 1)
          @include adaptive-value('line-height', 55, 21, 1)
          text-align: center
          margin-bottom: 30px
          @media (min-width: 577px)
            display: none
        &__list
          margin-top: 20px
          @media (min-width: 577px)
            margin-top: 0
        &__item
          display: none
          @include adaptive-value('margin-bottom', 50, 5, 1)
          @include adaptive-value('padding-top', 40, 5, 1)
          border-top: 1px solid $light-blue
          @media (min-width: 321px)
            display: block
          @media (min-width: 577px)
            display: none
            margin-bottom: 0
            border-top: none
          @media (min-width: 961px)
            display: block
            padding-top: 20px
          &:nth-of-type(-n+3)
            display: block
          &:nth-of-type(1)
            border-top: none
            padding-top: 0
        &__link
          display: block
          color: #fff
          font-size: 15px
          @include adaptive-value('line-height', 50, 19, 1)
          letter-spacing: .02em
          @media (min-width: 376px)
            line-height: 21px
        .see-all
          @include adaptive-value('margin-top', 20, 10, 1)
          @media (min-width: 376px)
            margin-top: 20px
          @media (min-width: 577px)
            margin-top: 25px
          @media (min-width: 1200px)
            margin-top: 53px
      &:nth-of-type(2)
        .descr-block
          left: calc(-100% - 6px)
      &:nth-of-type(3)
        .descr-block
          left: calc(-200% - 12px)
      &.active
        .title-block
          background-color: #1F8BED
          backdrop-filter: blur(40px)
        .descr-block
          z-index: 10
          opacity: 1























