@import "../_mixins/mixins_list"
.header
  @media (min-width: 1200px)
    max-width: 100vw
  &.scroll
    position: fixed
    top: 0
    left: 0
    width: 100vw
    background-color: #fff
    z-index: 20
    transform: translateY(-120%)
    transition: none
    border-bottom: 1px solid $blue2
    @media (min-width: 1200px)
      padding-right: 8px
    &.show
      transition: transform .3s
      transform: translateY(0)
    .header__content__top
      display: none !important
    .header__content__bottom
      padding: 5px 0
      &:after
        display: none
      .hbottom-backcall
        display: block !important
        white-space: nowrap
        margin: 0
        order: 5
        .backcall-link
          padding: 9px 12px
      .logo
        flex: 0 0 75px
        max-width: 75px
        margin: 0 20px -20px 0
        .logo-link
          position: relative
          left: 0
          bottom: 0
      .hbottom-search
        @media (min-width: 1200px)
          display: block !important
          order: 4
          margin: 0
          z-index: 1
          margin-right: 15px
          .search-form
            .input-group
              display: flex
              align-items: center
              position: relative
              max-width: 100vw
              button
                background-color: transparent
                display: inline-flex
                align-items: center
                justify-content: center
                border: none
                padding: 10px 9px
                position: absolute
                left: 5px
                top: 0
                i.icon
                  font-size: 18px
                  line-height: 18px
                  margin-top: 1px
                  &:before
                    font-weight: 500
              input
                position: relative
                right: 0
                width: 100%
                min-width: 220px
                background-color: $blue-blue
                border: 1px solid transparent
                border-radius: 4px
                padding: 9px 15px 8px 45px
                font-size: 15px
                line-height: 21px
                letter-spacing: .02em
                transition: background-color .3s, border-color .3s, max-width .3s
                @include input-placeholder
                  color: $dark-blue
                  opacity: 1
                  transition: opacity .3s
                &:focus
                  background-color: #fff
                  border-color: #1D6AFF
                  @include input-placeholder
                    opacity: .3
      .hbottom-menu
        .main-menu__list
          margin: 0 0 0 15px
  &__content
    &__top
      @media (min-width: 1200px)
        display: flex !important
        position: relative
        color: #fff
        padding: 28px 0 28px 170px
        font-size: 15px
        line-height: 20px
        letter-spacing: .02em
        &:before
          content: ''
          display: block
          width: 100vw
          height: 100%
          background-color: #009EF6
          position: absolute
          left: 50%
          top: 0
          z-index: -1
          transform: translateX(-50%)
        .htop-time
          margin-left: 28px
          display: inline-block
        .htop-email
          margin-left: 29px
          .mail-link
            color: #fff
        .htop-phone
          margin-left: 28px
          .phone-link
            color: #fff
        .htop-backcall
          margin: -12px 0 -12px auto
          .backcall-link
            padding: 12px 16px
            font-size: 15px
            line-height: 20px
            letter-spacing: .02em

    &__bottom
      display: flex
      align-items: center
      justify-content: flex-start
      padding: 5px 15px 0
      position: fixed
      top: 0
      left: 0
      width: 100%
      background-color: #fff
      z-index: 20
      border-bottom: 1px solid $blue2
      @media (min-width: 577px)
        padding: 5px 24px 3px
      @media (min-width: 1200px)
        position: relative
        padding: 15px 0 17px
        border-bottom: none
        &:after
          content: ''
          display: block
          width: 100vw
          height: 100%
          position: absolute
          left: 50%
          right: 50%
          transform: translateX(-50%)
          bottom: 0
          box-shadow: 0px 4px 20px rgba(47, 73, 113, 0.06)
          z-index: -1
      .hbottom-phone
        @media (min-width: 577px)
          display: block !important
          order: 2
          margin-left: 20px
          .phone-link
            display: inline-block
            font-weight: 800
            font-size: 20px
            line-height: 20px
            letter-spacing: -.04em
            color: $dark-blue
            transition: color .3s
            &:hover
              color: $sblue
        @media (min-width: 1200px)
          display: none !important
      .logo
        flex: 0 0 75px
        display: block
        margin: 0 auto -15px
        .logo-link
          display: block
        @media (min-width: 577px)
          order: 1
          margin: 0
        @media (min-width: 1200px)
          flex: 0 0 140px
          margin: 0
          position: relative
          height: 100%
          .logo-link
            display: block
            width: 100%
            max-width: 100%
            position: absolute
            left: -21px
            bottom: -31px
          .logo-image
            width: 100%
            height: auto
      .hbottom-backcall
        i.icon
          font-size: 22px
          line-height: 20px
          color: $dark-blue
          &:before
            font-weight: 600
        @media (min-width: 577px)
          order: 3
          margin-left: auto
          z-index: 1
          .backcall-link
            background-color: $dark-blue
            color: #fff
            border-radius: 4px
            transition: background-color .3s
            font-weight: 500
            font-size: 16px
            line-height: 16px
            letter-spacing: .025em
            padding: 8px 12px
            &:hover
              background-color: #1D6AFF
            &:active
              background-color: $darky

          i.icon
            display: none
          span
            display: inline-block !important
        @media (min-width: 1200px)
          display: none
      .hbottom-menu
        @import "../_plugins/_hamburger"
        .main-menu
          display: block
          background-color: #fff
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100vh
          padding: 11px 15px
          overflow-y: scroll
          @media (min-width: 577px)
            display: block !important
            width: 284px
            left: auto
            right: 0
            top: 0
            transform: translateX(100%)
            transition: transform .3s
            padding-top: 76px
            &.show
              transform: translateX(0)
          @media (min-width: 1200px)
            width: auto
            max-width: 100%
            height: auto
            position: relative
            transform: none !important
            overflow: visible
            padding: 0
          &__search
            @media (min-width: 577px)
              display: none
            .input-group
              max-width: calc(100% - 40px)
              position: relative
              button
                background-color: transparent
                display: inline-flex
                align-items: center
                justify-content: center
                border: none
                padding: 10px 14px
                position: absolute
                left: 0
                top: 0
                i.icon
                  color: $dark-blue
                  margin-top: -1px
              input
                width: 100%
                flex: 1 1 100%
                background-color: $blue-blue
                border: 1px solid transparent
                border-radius: 4px
                padding: 8px 14px 8px 40px
                font-size: 15px
                line-height: 21px
                letter-spacing: .02em
                transition: background-color .3s, border-color .3s
                @include input-placeholder
                  color: $dark-blue
                  opacity: 1
                  transition: opacity .3s
                &:focus
                  background-color: #fff
                  border-color: #1D6AFF
                  @include input-placeholder
                    opacity: .3
          &__list
            margin: 25px 0
            @media (min-width: 1200px)
              display: flex
              flex-wrap: wrap
              margin: 0 0 0 30px
          &__item
            display: block
            margin-bottom: 24px
            @media (min-width: 577px)
              text-align: center
            @media (min-width: 1200px)
              margin: 0 35px 0 0 !important
            .icon-arrow-right_icon
              display: inline-block
              margin-left: 8px
              &:before
                transform: rotate(90deg)
                transition: transform .3s
              &.show
                &:before
                  transform: rotate(90deg) scale(-1,1)
            &.submenu
              margin-bottom: 15px
              position: relative
              .submenu-list
                width: 100%
                padding: 10px 0 10px 15px
                margin: 10px 0 0
                border-top: 1px solid #eee
                border-bottom: 1px solid #eee
                @media (min-width: 577px)
                  padding: 10px 0
                @media (min-width: 1200px)
                  position: absolute
                  left: 0
                  top: calc(100% - 15px)
                  background-color: #fff
                  width: auto
                  min-width: 365px
                  text-align: left
                  padding: 15px
                  border: 1px solid #eee
                  border-radius: 6px
                &__item
                  display: block
                  margin-bottom: 8px
                  border-bottom: 1px solid #eee
                  padding-bottom: 8px
                  &:last-child
                    margin-bottom: 0
                    border-bottom: none
                    padding-bottom: 0
                &__link
                  display: inline-block
                  font-size: 16px
                  line-height: 20px
                  font-weight: 700
                  color: $sdark-blue
                  transition: color .3s
                  &:hover
                    color: $dark-blue
              &:hover
                @media (min-width: 1200px)
                  .submenu-list

                    display: block !important



            &:last-child
              margin-bottom: 0
          &__link
            display: inline-block
            font-size: 18px
            line-height: 22px
            font-weight: 700
            color: $sdark-blue
            transition: color .3s
            @media (min-width: 1200px)
              font-size: 15px
              letter-spacing: .02em
              padding: 10px 0
            &:hover
              color: $dark-blue
          &__footer
            display: flex
            flex-direction: column
            border-top: 1px solid rgba($sdark-blue, .2)
            padding-top: 22px
            @media (min-width: 577px)
              align-items: center
            @media (min-width: 1200px)
              display: none
            .phone-link
              display: inline-block
              color: $sdark-blue
              font-weight: 800
              font-size: 28px
              line-height: 28px
              letter-spacing: -.02em
              transition: color .3s
              &:hover
                color: $dark-blue
            .mail-link
              display: inline-block
              margin-top: 24px
              font-weight: 500
              font-size: 16px
              line-height: 22px
              text-decoration: underline
              color: $sdark-blue
              transition: color .3s
              &:hover
                color: $dark-blue
            .fcall-link
              width: 200px
              margin-top: 25px
              padding: 14px 0
              border-radius: 6px
        @media (min-width: 577px)
          order: 5
        @media (min-width: 1200px)
          order: 1
          flex: 1 1 100%
          max-width: 100%
          .hamburger
            display: none !important
      .hbottom-search
        @media (min-width: 577px)
          display: block !important
          order: 4
          margin: 0 20px
          z-index: 1
          .search-form
            .input-group
              display: flex
              align-items: center
              position: relative
              max-width: 100vw
              button
                background-color: transparent
                display: inline-flex
                align-items: center
                justify-content: center
                border: none
                padding: 10px 9px
                position: relative
                left: 0
                top: 0
                i.icon
                  font-size: 22px
                  line-height: 22px
                  color: $dark-blue
                  margin-top: -1px
                  &:before
                    font-weight: 800
              input
                position: absolute
                right: 50px
                width: calc(100vw - 230px)
                background-color: $blue-blue
                border: 1px solid transparent
                border-radius: 4px
                padding: 8px 15px
                font-size: 15px
                line-height: 21px
                letter-spacing: .02em
                transition: background-color .3s, border-color .3s, max-width .3s
                @include input-placeholder
                  color: $dark-blue
                  opacity: 1
                  transition: opacity .3s
                &:focus
                  background-color: #fff
                  border-color: #1D6AFF
                  @include input-placeholder
                    opacity: .3
        @media (min-width: 1200px)
          margin: 0 0 0 auto
          .search-form
            .input-group
              max-width: 270px
              position: relative
              input
                display: block !important
                order: 2
                position: relative
                right: 0
                width: 270px
                padding: 10px 15px 10px 40px
              button
                position: absolute
                left: 5px
                top: 0
                i.icon
                  font-size: 18px
                  line-height: 18px
                  margin-top: 1px
                  &:before
                    font-weight: 500










